@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf");
  font-weight: 300;
}

@font-face {
  font-family: Avenir;
  src: url("Avenir-Heavy.01d9ab74.ttf");
  font-weight: 500;
}

:root {
  --text-color: #3d4e61;
  --border-color: #e1e7ed;
}

body {
  box-sizing: border-box;
  margin: 0;
}

.container {
  flex-direction: column;
  align-items: center;
  padding: 20px 8px 0;
  display: flex;
}

.form {
  width: 100%;
  display: flex;
}

.form--margin {
  margin-bottom: 20px;
}

.form__input {
  border: 1px solid var(--border-color);
  color: var(--text-color);
  border-radius: 4px;
  flex: 1;
  padding: 0;
  font-family: Avenir, sans-serif;
  font-weight: 100;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input--big {
  height: 68px;
  padding-left: 62px;
  font-size: 16px;
}

.form__input--small {
  height: 40px;
  padding-left: 33px;
  font-size: 14px;
}

.form__icon {
  display: block;
  position: relative;
}

.form__icon:before {
  content: "";
  background-image: url("Search.ccd3c07b.svg");
  background-repeat: no-repeat;
  background-size: contain;
  position: absolute;
}

.form__icon--big:before {
  width: 19px;
  height: 19px;
  top: 25px;
  left: 26px;
}

.form__icon--small:before {
  width: 11px;
  height: 11px;
  top: 15px;
  left: 13px;
}

.form__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input:focus {
  color: var(--text-color);
  text-shadow: 1px 2px 4px #969696;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  outline: none;
}

